.container{
    height: 100%;
    display:flex;
    justify-self: center;
}
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-self: center;
    align-items: center;
    font-weight: 300;
    margin:0 auto;
}
.movies {
    display:grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width:80%;
    padding-top: 70px;
    margin:0 auto;
}

@media screen and (max-width:1090px){
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}